.social {

	display: flex;
	// .social__item

	&__item {

		flex-shrink: 0;

		svg {
			rect {
				transition: .3s fill;
			}
		}

		@media (any-hover: hover) {
			&:hover {
				svg rect {
					fill: $red;
				}
			}
		}
	}

	&__link {
		@extend %flex-center;
		width: 100%;
		height: 100%;
	}
}
